import "./styles.css";

let buttons = Array.from(document.getElementsByTagName("button"));
let canvas = document.getElementById("canvas");
canvas.width = 600;
canvas.height = 600;
let ctx = canvas.getContext("2d");

let crab = new Image();
crab.src = "assets/crab.png";

buttons.forEach((button) => {
  button.addEventListener("click", () => {
    let size = 1 + 10 * Math.random();
    ctx.drawImage(
      crab,
      (canvas.width - 150) * Math.random(),
      (canvas.height - 150) * Math.random(),
      crab.width / size,
      crab.height / size
    );
  });
});
